timeline {
  display: flex;
  margin: 0 auto;
  flex-direction: column;
  align-items: flex-start;
}

timeline .timeline {
  position: relative;
  max-width: 300px;
}

timeline .timeline:last-child:after {
  display: none;
}

timeline .timeline:after {
  width: 8px;
  background-color: #d6e9ff;
  top: 0;
  bottom: -1px;
  left: 66%;
  content: "";
  display: block;
  position: absolute;
  border-top-left-radius: 7px;
  -webkit-border-top-left-radius: 7px;
  -moz-border-radius-topleft: 7px;
  border-top-right-radius: 7px;
  -webkit-border-top-right-radius: 7px;
  -moz-border-radius-topright: 7px;
}

timeline .timeline-container {
  padding: 10px 0;
  position: relative;
  background-color: inherit;
  width: 50%;
}

timeline .timeline-container:after {
  width: 8px;
  height: 8px;
  right: -21px;
  background-color: #fff;
  border: 1px solid #cbdce9;
  top: 0;
  border-radius: 50%;
  z-index: 1;
  content: "";
  display: block;
  position: absolute;
}

timeline .left,
timeline .right {
  position: absolute;
  top: -18px;
}

timeline .left {
  right: 50%;
}

timeline .right {
  right: -29%;
}

timeline .content {
  padding: 20px 54px 30px 31px;
  position: relative;
  border-radius: 6px;
}

